.s_body {
  display: flex;
  justify-content: center;
  align-items:flex-start;
  flex-direction: row;
  width: 100%;
  padding-left: 2%;
  padding-right: 2%;
  /* border: 1px solid #3f9bcb; */
}

.s_body_left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 60%;
  gap: 20px;


}

.s_body_right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 40%;
  gap: 20px;


}

.s_body_box {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  width: 70%;
  gap: 10px;

}
.s_r_body_box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 50%;
  gap: 10px;
}

.right_box_image {
  width: 80%;
  height: 80%;
  border-radius: 50px;
  transition: linear 0.2s;
}

.right_box_image:hover {
  /* width: 75%;
  height: 75%; */
  border-radius: 50px;
  /* transition: linear 0.2s; */
  /* transform: scale(1.25); */
  /* reduce scale by 25% */
  transform: scale(0.95);
  overflow: hidden;
}
.left_box {
  width: 50%;
}
.right_box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}
.right_box_title {
  font-weight: bold;
}
.right_box_subtitle {
  font-style: italic;
  color: #3f9bcb;
}
.right_box_content {
  font-size: medium;
  justify-content: center;
  color: #3f9bcb;
}

@media screen and (max-width: 1024px) {
  .s_body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 10px;
  }
  .s_body_left {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 20px;
  }
  .s_body_right{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap: 20px;
  }
}
