.p_body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  gap: 20px;
  overflow-y: hidden;
}
.p_row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  gap: 20px;
  overflow-y: hidden;
}
.p_image {
  transition: linear 0.2s;
  cursor: pointer;
  overflow-y: hidden;
}
.p_image:hover {
  transform: scale(0.95);
  transition: linear 0.2s;
}

@media screen and (max-width: 786px) {
  .p_row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 100%;
    gap: 20px;
    overflow-y: hidden;
  }
}
.modal_image {
  transition: linear 0.2s;
  overflow-y: hidden;
}
.modal {
  position: fixed;
  z-index: 1;
  padding-top: 2%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.874);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 20px;

  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.modal::-webkit-scrollbar {
  display: none;
}

.modal_head {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: row;
  top: 0;
  left: 0;
  width: 100%;
  padding-right: 20px;
}
.close {
  color: white;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
}
.modal_body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
}
