.header_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 15px;
    font-size: larger;
    background-color: #282c34;
    /* border: 1px solid turquoise; */
  
    /* position: sticky; */
    /* top: 0; */
    /* z-index: 1; */
  }
  .header_container_imagebar_connect {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    /* border: 1px solid black; */
    gap: 10px;
  }
  .header_container_nav {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 40px;
    font-size: larger;
    border-bottom: 1px solid #79a4d1;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
    /* position: sticky;
    top: 0;
    z-index: 1; */
  }
  .header_container_imagebar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    border-bottom: 1px solid #3b6cae;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
  }
  .sticky {
    position: sticky;
    /* z-index: 10; */
    overflow: hidden;
    top: 0;
  }
  
  #logo {
    width: 100px;
    height: 80px;
  }
  #icon {
    cursor: pointer;
  }
  
  @media screen and (max-width: 1024px) {
    .header_container_nav {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      gap: 40px;
      font-size: larger;
      padding-left: 2%;
      width: 100%;
      border-bottom: 1px solid #79a4d1;
      padding-top: 5px;
      padding-bottom: 5px;
    }
    .phone {
      display: none;
    }
    .sm {
      display: none;
    }
    .header_container_imagebar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      border-bottom: 1px solid #3b6cae;
      padding-left: 2%;
      padding-right: 2%;
      width: 100%;
      padding-top: 5px;
      padding-bottom: 5px;
    }
    #logo {
      width: 80px;
      height: 60px;
    }
    .burger {
      display: flex;
    }
  }
  
  
@media screen and (min-width: 1024px) {
    .burger {
      display: none;
    }
  }
  