.box_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-top: 5px;
  /* border: 1px solid green; */
  gap: 20px;
}

.box_container_head {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
}

p {
  font-weight: bold;
  text-align: justify;
}

.button {
  background-color: #3b6cae;
  color: white;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}
.call:hover {
  background-color: #79a4d1;
  color: #3b6cae;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}
.box_font {
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

@media screen and (max-width: 1024px) {
  .p {
    display: none;
  }
}

.box_container_body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  flex-wrap: wrap;
  gap: 15px;

  /* border: 1px solid rgb(255, 255, 255); */
}

.box_container_body_title {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;

  /* border: 1px solid rgb(255, 0, 0); */
}

.box_container_body_row {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  width: 100%;
  padding-left: 2%;
  padding-right: 2%;
  gap: 10px;
}

@media screen and (max-width: 1024px) {
  .box_container_body_row {
    flex-direction: column;
    gap: 10px;
  }
}
.saying {
  /* make font italic */
  font-style: italic;
}
.box_container_body_row_tile {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: row;
  width: 100%;
  height: 20%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: rgba(33, 54, 80, 0.5);
  color: #3f9bcb;
  border-radius: 12px;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding-left: 2%;
  padding-right: 2%;
  gap: 10px;
  /* border: 1px solid black; */
}
.tile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 20%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: rgba(33, 54, 80, 0.5);
  color: #3f9bcb;
  border-radius: 12px;
}
.box_container_body_row_boss {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 20%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: rgba(33, 54, 80, 0.5);
  color: #3f9bcb;
  border-radius: 12px;
}

.box_container_body_row_left {
  display: flex;
  justify-content: baseline;
  align-items: center;
  flex-direction: column;
}
.box_container_body_row_left_icon {
  width: 30px;
  height: 30px;
  color: white;
}
.box_container_body_row_right {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 70%;
  gap: 10px;
}
.box_container_body_row_right_title {
  font-weight: bolder;
  color: white;
}
.tile_right_title {
  font-weight: bolder;
  color: white;
}
.u_l {
  margin-top: 20px;
  list-style-type: none;
}
.l_i {
  font-size: medium;
  font-weight: bold;
  margin-top: 5px;
}
.span_arrow {
  color: white;
  font-size: large;
  margin-right: 5px;
}
