.c_body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  /*border: 1px solid turquoise;*/
}
.c_row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  width: 100%;
  gap: 20px;
  padding-left: 4%;
  padding-right: 4%;
  /*border: 1px solid turquoise;*/
}
@media screen and (max-width: 786px) {
  .c_row {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    padding-left: 4%;
    padding-right: 4%;
    gap: 20px;
    /*border: 1px solid turquoise;*/
  }
}

.c_u_l {
  /* remove list bullets */
  list-style-type: none;
}

.c_row_map {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  width: 100%;
  padding-left: 4%;
  padding-right: 4%;
  gap: 20px;
  /*border: 1px solid turquoise;*/
}
@media screen and (max-width: 786px) {
  .c_row_map {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding-left: 4%;
    padding-right: 4%;
    gap: 20px;
    /*border: 1px solid turquoise;*/
  }
}
.c_col {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  margin-top: 20px;
  /* gap: 15px; */
  /*border: 1px solid turquoise;*/
}

.c_col_top {
  font-weight: bold;
}

.c_col_bottom {
  margin-bottom: 10px;
}

.c_col_top_span {
  margin-right: 20px;
}

.map {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 20px;
  /*border: 1px solid turquoise;*/
}
.c_col_contactus {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* width: 100%; */
  gap: 20px;
  /*border: 1px solid turquoise;*/
}

.input_block {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  gap: 5px;
  /*border: 1px solid turquoise;*/
  font-size: x-large;
  font-weight: bold;
}
.input_field {
  width: 100%;
  height: 40px;
  /* max-width: 300px; */
  min-width: 300px;
  max-height: 100px;
  /*border: 1px solid turquoise;*/
  border-radius: 5px;
  font-size: x-large;
  font-weight: bold;
  padding-left: 10px;
}
.input_field_textarea {
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  height: 40px;
  min-height: 100px;
  max-height: 100px;
  /*border: 1px solid turquoise;*/
  border-radius: 5px;
  font-size: x-large;
  font-weight: bold;
  padding-left: 10px;
}

.submit {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 40px;

  border-radius: 5px;
  font-size: x-large;
  font-weight: bold;
  background-color: #3f9bcb;

  color: #3b6cae;
  cursor: pointer;
}
.submit:hover {
  background-color: #151c20;
}
